.banner-container img {
    width: 100%;
    height: auto;
  }
  
  .banner-container h1 {
    position: absolute;
    top: 50%;
    left: 20%;
    transform: translate(-50%, -50%);
    z-index: 1;
    color: #fff;
  }
  
  .GetIn_Touch h1 {
    font-family: "Poppins", sans-serif;
    text-align: center;
    margin-top: 100px;
    font-size: 35px;
    font-weight: 500;
    letter-spacing: 1px;
    color: black;
  }
  .black-text-contact {
    font-family: "Poppins", sans-serif;
    align-items: center;
    font-size: 35px;
    font-weight: 600;
    letter-spacing: 1px;
    display: inline;
    color: #1354a4;
  }
  
  .Contact_Cards {
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    margin-top: 20px;
    margin-bottom: -10px;
   
  }
  
  
  .card_main1 {
    width: "100%";
    height: 500px;
    border-radius: 20px;
  }
  .card_main2 {
    width: "100%";
    height: 500px;
    border-radius: 20px;
  }
  .Contact_main {
    margin-top: 100px;
  }
  
  
  
  .contact_h2{
    font-family: "Poppins", sans-serif;
    text-align: start;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 1px;
    color: black;
  }
  .contact_h4{
    font-family: "Poppins", sans-serif;
    text-align: start;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1px;
    color: black;
  }
  
  
  
  .contsct_para h1 {
    font-family: "Poppins", sans-serif;
    text-align: center;
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 1px;
    color: #1354a4;
  }
